$color__background-body: #fff;
$color__background-screen: #f1f1f1;
$color__background-hr: #ccc;
$color__background-button: #e6e6e6;
$color__background-pre: #eee;
$color__background-ins: #fff9c0;

$color__text-screen: #21759b;
$color__text-input: #666;
$color__text-input-focus: #111;
$color__link: #4169e1; //royalblue
$color__link-visited: #800080; //purple
$color__link-hover: #191970; //midnightblue
$color__text-main: #404040;

$color__border-button: #ccc #ccc #bbb;
$color__border-button-hover: #ccc #bbb #aaa;
$color__border-button-focus: #aaa #bbb #bbb;
$color__border-input: #ccc;
$color__border-abbr: #666;

// The Music Room Colors
$tmr-bg-light: rgb(203 139 67 / 80%);
$tmr-black: #1b1b1b;
$tmr-white: rgb(255, 255, 255);
$tmr-offwhite: #F1F1F1;
$tmr-background-light: $tmr-offwhite;
$tmr-brown: #614700;
$tmr-grey: grey;
$tmr-light-grey: rgb(213, 213, 213); 
$tmr-focus: rgb(228, 91, 0);
$tmr-blur:rgb(172 68 0);
$tmr-hover: rgb(0, 228, 190);

// typography color
$tmr-dark-type: #1b1b1b;
$tmr-light-type: #F1F1F1;
$tmr-typography: $tmr-dark-type;

// Input fill 
$tmr-dark-fill: #1b1b1b;
$tmr-light-fill: #e5e5e5;
$tmr-input-fill: $tmr-light-fill;
// Input text 
$tmr-dark-text: #1b1b1b;
$tmr-light-text: $tmr-offwhite;
$tmr-input-text: $tmr-light-text;




